<template>
  <div class="register">
    <div class="row mt-3">
      <div class="card mx-auto">
        <div class="card-header text-white bg-primary">Register</div>
        <div class="card-body">
          <form @submit.prevent="registerUser">
            <div class="form-group">
              <label for="name">Name</label>
              <input
                type="text"
                placeholder="Name"
                name="name"
                v-model="name"
                class="form-control"
                id="name"
                autocomplete="off"
                required
              />
            </div>
            <div class="form-group">
              <label for="username">Username</label>
              <input
                type="text"
                placeholder="Username"
                name="username"
                v-model="username"
                class="form-control"
                id="username"
                autocomplete="off"
                required
              />
            </div>
            <div class="form-group">
              <label for="email">Email</label>
              <input
                type="email"
                placeholder="Email"
                name="email"
                v-model="email"
                class="form-control"
                id="email"
                autocomplete="off"
                required
              />
            </div>
            <div class="form-group">
              <label for="password">Password</label>
              <input
                type="password"
                placeholder="Password"
                name="username"
                v-model="password"
                class="form-control"
                id="password"
                autocomplete="off"
                required
              />
            </div>
            <div class="form-group">
              <label for="confirmPassword">Confirm Password</label>
              <input
                type="password"
                placeholder="Repeat Password"
                name="confirmPassword"
                v-model="confirmPassword"
                class="form-control"
                id="confirmPassword"
                autocomplete="off"
                required
              />
            </div>
            <input type="submit" value="Login" class="btn btn-primary btn-block" />
            <!-- &nbsp;&nbsp;&nbsp;&nbsp; -->
            <router-link to="/login" class="card-link">Already have an account?</router-link>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapActions } from "vuex";

export default {
  name: "Register",
  components: {},
  data() {
    return {
      name: "",
      username: "",
      email: "",
      password: "",
      confirmPassword: "",
    };
  },
  methods: {
    ...mapActions(["register"]),
    registerUser() {
      let user = {
        name: this.name,
        username: this.username,
        email: this.email,
        password: this.password,
        confirmPassword: this.confirmPassword,
      };
      this.register(user).then((res) => {
        if (res.data.success) {
          this.$router.push("/login");
        }
      });
    },
  },
};
</script>
<style scoped>
.register {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.card {
  width: 60vh;
}
</style>
